.nav {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-template-rows: 1fr;
    grid-template-areas: "n1 n2 n3 n4 n5";
    grid-area: n;
    border: 2px solid black;
    grid-column: 6/12;
    height: 50px;
    text-align: center;
}

.nav a {
    color: black;
    font-weight: bold;
    text-decoration: none;
}

.nav a.active {
    color: yellow;
}

.nav1 {
    grid-area: n1;
    align-self: center;
}

.nav2 {
    grid-area: n2;
    align-self: center;
}

.nav3 {
    grid-area: n3;
    align-self: center;
}

.nav4 {
    grid-area: n4;
    align-self: center;
}

.nav5 {
    grid-area: n5;
    align-self: center;
}

a:hover {
    color: magenta;
}

@media screen and (max-device-width: 850px) {
    .nav {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        grid-template-areas: "n1" "n2" "n3" "n4" "n5";
        grid-area: n;
        border: 2px solid black;
        grid-column: 2/3;
        height: 150px;
        text-align: center;
        align-self: center;
    }
}

@media screen and (max-device-width: 500px) {
    .nav {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        grid-template-areas: "n1" "n2" "n3" "n4" "n5";
        grid-area: n;
        border: 2px solid black;
        grid-column: 2/3;
        height: 150px;
        text-align: center;
        align-self: center;
    }
}



